// eslint-disable-next-line no-use-before-define
import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {
  Calendar,
  CalendarVariant,
  CalendarChangePayload,
  CalendarSelectPayload,
  CalendarDayOfWeek,
  CalendarMonthFormat,
  CalendarYearFormat,
  CalendarWeekdayFormat,
  CalendarDayFormat,
} from '@here/hds-calendar';

// eslint-disable-next-line no-new
new Calendar();
interface HDSCalendarProperties
  extends Omit<React.HTMLAttributes<HTMLElement>, 'onSelect' | 'onChange'> {
  variant?: CalendarVariant;
  hideYearControls?: boolean;
  hideMonthControls?: boolean;
  firstDayOfWeek?: CalendarDayOfWeek;
  lang?: string;
  monthFormat?: CalendarMonthFormat | 'none';
  yearFormat?: CalendarYearFormat | 'none';
  weekdayFormat?: CalendarWeekdayFormat | 'none';
  dayFormat?: CalendarDayFormat | 'none';
  dayOutsideFormat?: CalendarDayFormat | 'none';
  value?: string;
  selected?: string;
  /* eslint-disable no-unused-vars */
  onSelect: (e: CustomEvent<CalendarSelectPayload>) => void;
  onChange: (e: CustomEvent<CalendarChangePayload>) => void;
  /* eslint-enable no-unused-vars */
}

const HDSCalendar = React.forwardRef((props: HDSCalendarProperties, ref) => {
  const {children, className, onSelect, onChange, ...rest} = props;

  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<CalendarChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e);
  };

  const _handleSelect = (e: CustomEvent<CalendarSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e);
  };

  useEffect(() => {
    if (innerRef.current) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
      innerRef.current.addEventListener('select', _handleSelect as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleChange as EventListener);
      innerRef.current?.removeEventListener('select', _handleSelect as EventListener);
    };
  }, [onSelect, onChange, innerRef]);

  return (
    <hds-calendar ref={innerRef} {...(className && {class: className})} {...rest}>
      {children && children}
    </hds-calendar>
  );
});

HDSCalendar.displayName = 'HDSCalendar';

HDSCalendar.defaultProps = {
  variant: 'default',
  firstDayOfWeek: 'Monday',
  lang: 'en-US',
  monthFormat: 'long',
  yearFormat: 'numeric',
  weekdayFormat: 'short',
  dayFormat: 'numeric',
  dayOutsideFormat: 'numeric',
  value: '',
  selected: '',
  hideYearControls: false,
  hideMonthControls: false,
};

export default HDSCalendar;
